import React, { useEffect, useLayoutEffect } from "react";
import {
  useLocation,
  Link,
  useParams,
  Navigate,
  useNavigate,
} from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import {
  add,
  userData,
  user,
  authCheck,
  proudctbyid,
  oneitem,
  loding,
} from "../feature/authslice";
import Footer from "./Footer";

export default function Proudctdetail() {
  const location = useParams();
  const navi = useNavigate();

  const profile = useSelector(user);
  const loading = useSelector(loding);

  const uid = useSelector(userData);
  const dat = useSelector(oneitem);
  const dispatch = useDispatch();
  useLayoutEffect(() => {
    dispatch(proudctbyid(location.id));
  }, []);

  return (
    <>
      {loading || !dat ? (
        <div
          className="w-full   flex justify-center "
          style={{ minHeight: "83vh" }}
        >
          {" "}
          <span className="loading loading-spinner text-secondary w-14"></span>
        </div>
      ) : (
        <div
          style={{ display: "flex", flexDirection: "column", height: "90.8vh" }}
        >
          <section className="text-gray-700 body-font overflow-hidden bg-white flex justify-center w-full">
            <div className="container px-5 py-24 mx-auto lg:w-9/12">
              <div className=" mx-auto flex flex-wrap">
                <div className=" ">
                  <img
                    alt="ecommerce"
                    className=" object-cover object-center   ml-8  lg:mt-12 max-w-sm max-h-80"
                    src={dat.image}
                  />
                </div>

                <div className="lg:w-1/2 w-full lg:pl-10 lg:py-6 mt-6 lg:mt-0">
                  <h1 className="text-gray-900 text-3xl title-font font-medium mb-1">
                    {dat.title}
                  </h1>
                  <div className="flex mb-4"></div>
                  <p className="leading-relaxed">
                    Fam locavore kickstarter distillery. Mixtape chillwave
                    tumeric sriracha taximy chia microdosing tilde DIY. XOXO fam
                    indxgo juiceramps cornhole raw denim forage brooklyn.
                    Everyday carry +1 seitan poutine tumeric. Gastropub blue
                    bottle austin listicle pour-over, neutra jean shorts keytar
                    banjo tattooed umami cardigan.
                  </p>
                  <div className="flex mt-6 items-center pb-5 border-b-2 border-gray-200 mb-5">
                    {dat.category === "mob" ||
                    dat.category === "ele" ||
                    dat.category === "fash" ? (
                      <div className="flex">
                        <span className="mr-3">Color</span>
                        <button className="border-2 border-gray-300 rounded-full w-6 h-6 focus:outline-none"></button>
                        <button className="border-2 border-gray-300 ml-1 bg-gray-700 rounded-full w-6 h-6 focus:outline-none"></button>
                        <button className="border-2 border-gray-300 ml-1 bg-red-500 rounded-full w-6 h-6 focus:outline-none"></button>
                      </div>
                    ) : (
                      ""
                    )}

                    {dat.category === "fash" ? (
                      <div className="flex ml-6 items-center">
                        <span className="mr-3">Size</span>
                        <div className="relative">
                          <select className="rounded border appearance-none border-gray-400 py-2 focus:outline-none focus:border-red-500 text-base pl-3 pr-10">
                            <option>SM</option>
                            <option>M</option>
                            <option>L</option>
                            <option>XL</option>
                          </select>
                          <span className="absolute right-0 top-0 h-full w-10 text-center text-gray-600 pointer-events-none flex items-center justify-center">
                            <svg
                              fill="none"
                              stroke="currentColor"
                              stroke-linecap="round"
                              stroke-linejoin="round"
                              stroke-width="2"
                              className="w-4 h-4"
                              viewBox="0 0 24 24"
                            >
                              <path d="M6 9l6 6 6-6"></path>
                            </svg>
                          </span>
                        </div>
                      </div>
                    ) : (
                      ""
                    )}
                  </div>

                  <div className="flex">
                    <span className="title-font font-medium text-2xl text-gray-900">
                      Rs. {dat.price}{" "}
                      {dat.category === "fru" || dat.category === "veg"
                        ? "/Kg"
                        : ""}
                    </span>

                    {profile ? (
                      <button
                        className="flex ml-auto text-white bg-green border-0 py-2 px-6 h focus:outline-none hover:bg-red-600 rounded"
                        onClick={() => {
                          const ct = {
                            productid: dat._id,
                            userid: uid._id,
                            title: dat.title,
                            price: dat.price,
                            category: dat.category,
                            image: dat.image,
                          };

                          dispatch(add(ct));
                        }}
                      >
                        Add to Cart
                      </button>
                    ) : (
                      // <Link to={"/login"}>
                      //   <button className="flex ml-auto text-white bg-green border-0 py-2 px-6 h focus:outline-none hover:bg-red-600 rounded">
                      //     Add to Cart
                      //   </button>
                      // </Link>
                      <button
                        className="flex ml-auto text-white bg-green border-0 py-2 px-6 h focus:outline-none hover:bg-red-600 rounded"
                        onClick={() => navi("/login")}
                      >
                        Add to Cart
                      </button>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </section>

          <Footer />
        </div>
      )}
    </>
  );
}
